import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';

import { startUploading } from '../actions/data'
import { pathFile } from '../actions/path';


export const HomeScreen = () => {

    const dispatch = useDispatch();
    const history = useHistory();
    const [pathState, setPathState] = useState()
    const path = useSelector(state => state.path)
    let file;

    const handleFileChange = (e) => {
        file = e.target.files[0];
        console.log(file.name)
        setPathState(file)
        dispatch(pathFile(file.name))
    }

    const handleSubmit = () => {
        if (pathState) {
            dispatch(startUploading(pathState, history))
        }
    }

    return (

        <div className="home_container">
              <img class="img-ai" src="https://res.cloudinary.com/ddyumspiu/image/upload/v1630965526/fondo_ohun70.png"/>

            <div id="upload-area" className="form">
                <div className="upload-ui">
                    <div className="titulo">Drag and drop audio file here</div>
                    <p className="titulo">OR</p>
                    <label htmlFor="fileSelector" className="btn-secundario btn-blanco">CHOOSE FILE</label>
                    <input
                        id="fileSelector"
                        type="file"
                        name="file"
                        className="btn-secundario"
                        onChange={handleFileChange}
                        style={{ display: 'none' }}

                    />

                    <div className="file-name">{path}</div>
                </div>
            </div>

            <div className="contenedor-enviar">
                <button onClick={handleSubmit} className="btn-primario">SUBMIT</button>
            </div>
        </div>
    )
}
